import uniBadge from "@/components/uni-badge/uni-badge.vue"
import uniList from "@/components/uni-list/uni-list.vue"
import uniListItem from "@/components/uni-list-item/uni-list-item.vue"
import uniPopup from '@/components/uni-popup/uni-popup.vue'
import {mapState,mapMutations} from 'vuex'
export default{
	data(){
		return{
			showView:true,
			myData:{
				nickName:'',
				gender:'',
				code:'',
				avatarUrl:''
			},
			sessionId:''
		}
	},
	
	onLoad() {
		this.init()
	},
	computed: mapState(['hasLogin','uerInfo']),  
	methods:{
		...mapMutations(['login']),
		
		// 登录信息渲染
		init(){
			uni.getStorage({
				key: 'userInfo',
				success:(res) => {
					this.login(res.data);
					this.myData.avatarUrl = res.data.avatarUrl
					this.myData.nickName = res.data.nickName
					this.sessionId = res.data.sessionId
					if(res.data.gender == 2){
						this.myData.gender = '女'
					}else{
						this.myData.gender = '男'
					}
				} 
			})
		},
		
		// 提示先登录
		stopSwitch(){
			uni.showToast({
				title:'请先登录',
				icon:'none'
			})
		},
		
		// 跳转至登录页面
		gotoLogin(){
			uni.navigateTo({
				url:'../login/login'
			})
		},
		
		// 跳转至身份验证页面
		gotoIdentify(){
			uni.navigateTo({
				url:'../checkIdentify/checkIdentify?sessionId='+this.sessionId
			})
		},
		
		// 跳转至资料认证页面
		gotoPersonDetail(){
			uni.navigateTo({
				url:'../personDetail/personDetail?sessionId='+this.sessionId
			})
		},
		
		// 修改个人信息
		person(){
			uni.navigateTo({
				url:'../information/information?sessionId='+this.sessionId
			})
		},
		
		// 跳转至竞拍页面
		gotoBuy(){
			uni.navigateTo({
				url:'../record/buy?sessionId='+this.sessionId
			})
		},
		
		// 跳转至订单页面
		record(){
			uni.navigateTo({
				url:'../record/record?sessionId='+this.sessionId
			})
		},
		
		// 跳转至活动页面
		activity(){
			uni.navigateTo({
				url:'../record/activity?sessionId='+this.sessionId
			})
		},
		
		// 跳转至购物车页面
		buyCar(){
			uni.navigateTo({
				url:'../record/buyCar?sessionId='+this.sessionId
			})
		},
		
		// 跳转至募捐页面
		donation(){
			uni.navigateTo({
				url:'../record/myDonation?sessionId='+this.sessionId
			})
		}
	},
	components:{
		uniBadge,
		uniList,
		uniListItem,
		uniPopup
	}
}
